html { scroll-behavior: smooth; font-size: 10px; }

a { color: inherit; text-decoration: none; }

a:hover { color: #FFFFFF; text-decoration: none; }

img { width: 100%; }

header { height: 0px; background: none; position: relative; top: 36px; left: 0; z-index: 999; }

header .logo { display: flex; align-items: center; position: relative; top: 2px; left: -30px; }

header .logo a { text-decoration: none; }

header .logo h2 { font-family: Raleway; font-weight: 400; font-size: 3.4rem; color: white; }

header .head-ul { height: 100%; align-items: center; display: flex; margin-left: 79px; }

header .head-ul .head-li { font-family: Montserrat; font-weight: 300; font-size: 14px; text-decoration: none; display: inline-block; }

header .head-ul .head-li :nth-child(1) { position: relative; left: 6px; }

header .head-ul .head-li a { color: #FFFFFF; }

header .head-ul .head-li a:hover { color: #ac9b86; text-decoration: none; }

header .logos { height: 100%; display: flex; margin-left: 21px; align-items: center; }

header .logos li { margin-right: 17px; display: inline-block; color: #f6f6f6; font-size: 2.0rem; font-weight: 200; }

header .logos li a:hover { text-decoration: none; color: #ac9b86; }

.container-about-us { max-width: 1195px; padding-bottom: 100px; }

.head-li:not(:last-child) { margin-right: 28px; }

.head-li a { color: white; }

.slider { position: relative !important; padding-top: 377px; padding-bottom: 380px; text-align: center; background: url("../images/fon.jpg") no-repeat center center; }

.slider:before { content: ''; position: absolute; top: 0; left: 0; width: 100%; height: 100%; background: rgba(0, 0, 0, 0.6); }

.slider p { position: relative; font-family: Raleway; font-size: 3.6rem; font-weight: 500; color: #ffffff; }

.slider h1 { position: relative; font-family: Raleway; font-weight: 800; font-size: 5.3rem; color: #ffffff; margin-top: 12px; margin-bottom: 15px; position: relative; left: -3px; }

.slider span { position: relative; font-family: Montserrat; font-weight: 300; font-size: 1.6rem; color: #ebebeb; line-height: 25px; margin-bottom: 55px; }

.slider .main-click { position: relative; width: 170px; height: 55px; background: #998675; font-family: Raleway; font-weight: 900; font-size: 14px; color: #ffffff; border: 1px solid #998675; text-decoration: none; display: flex; justify-content: center; align-items: center; margin: 0 auto; border-radius: 5px; border-bottom: 3px solid #736357; margin-top: 47px; }

.slider .main-click:hover { background: #d2af8f; }

.owl-dots { display: flex; align-items: center; justify-content: center; position: absolute; bottom: 36px; left: 0; right: 0; margin: auto; }

.owl-slider .owl-nav { bottom: 40px; left: 0; right: 0; margin: auto; z-index: 99; }

.owl-slider .owl-nav .owl-next { background: url(../images/right.png); background-size: cover !important; position: absolute; bottom: 46%; right: 7.5%; height: 73px; width: 40px; }

.owl-slider .owl-nav .owl-next:hover { background: url(../images/arrow-right.png); }

.owl-slider .owl-nav .owl-prev { background: url(../images/left.png); background-size: cover !important; position: absolute; bottom: 45%; left: 7.5%; height: 91px; width: 40px; }

.owl-slider .owl-nav .owl-prev:hover { background: url(../images/arrow-left.png); }

.owl-slider .owl-nav span { display: none; }

#about-us { padding-top: 74px; }

#about-us .box-real { height: 200px; width: 100%; display: flex; flex-direction: column; text-align: center; }

#about-us .box-real i { margin-bottom: 30px; font-size: 5.0rem; color: #555555; }

#about-us .box-real .icon-clone { font-size: 4.8rem; }

#about-us .box-real h1 { font-family: Raleway; font-weight: 600; font-size: 2.2rem; color: #555555; }

#about-us .box-real p { margin-top: 17px; font-family: Montserrat; font-weight: 300; font-size: 14px; color: #8c8c8c; line-height: 23px; position: relative; }

#about-us .box-real p:before { content: ''; background: #f1eee9; width: 75px; height: 3px; position: absolute; bottom: -31px; left: 0; right: 0; margin: auto; border-radius: 2px; }

#about-us .box-real p:after { content: ''; width: 0px; height: 3px; position: absolute; bottom: -31px; left: 0; right: 0; margin: auto; border-radius: 2px; }

#about-us .box-real:hover { cursor: pointer; }

#about-us .box-real:hover i { color: #c7b299; transition: all 0.6s; }

#about-us .box-real:hover h1 { color: #c7b299; transition: all 0.6s; }

#about-us .box-real:hover p:after { background: #c7b299; width: 75px; z-index: 9999; transition: all 0.6s; }

#about-us .about-us-content { max-width: 1261px; display: flex; flex-direction: column; text-align: center; padding-top: 79px; background: #fbfaf8; }

#about-us .about-us-content h1 { font-family: Raleway; font-weight: 600; font-size: 3.0rem; color: #555555; }

